<template>
    <div>
        <div class="row mb-1">
            <div class="col-lg-12 col-md-12 col-sm-12 col-xl-12 d-inline-flex mb-2">
                <slot name="button" />
                <button class="btn btn-primary m-1 justify-items-start" @click="handleActualizarDatos">
                    <i class="bx bx-refresh" style="font-size:20px"/> Actualizar
                </button>   
                <slot name="buttons" />
                <div class="spinner-border text-primary ms-auto" role="status" v-if="loading">
                    <span class="visually-hidden">Cargando...</span>
                </div>
            </div>
            <div class="col-lg-4 col-md-4 col-sm-12 col-xl-4">
                <div class="input-group mb-3">                  
                    <input type="search" class="form-control" v-on:keyup.enter="handleActualizarDatos"
                        placeholder="Busqueda" aria-label="Busqueda" 
                        v-model="busqueda" aria-describedby="button-addon2" >
                    <button class="btn btn-primary" type="button" @click="handleActualizarDatos" :disabled="loading">
                        <i class=" bx bx-search"></i>
                    </button>
                </div>
            </div>
            <slot name="row2"/>
        </div>
        <div class="table-responsive border border-primary">
            <table class="table table-bordered table-striped m-0" :class="classTable">
                <thead>
                    <tr>
                        <td v-for="(col, key) in columnas" :key="key" style="{{col.style}}" :class="col.class">
                            {{col.nombre}}
                        </td>
                    </tr>
                </thead>
                <tbody>
                    <tr v-if="data.length === 0">
                        <td :colspan="columnas.length+''" class="text-center">
                            <div>
                                <i class="bx bxs-data" :class="loading?'bx-tada':null" style="font-size:55px"></i>
                                <h5 class="mt-2">{{loading?'Cargando...':'No Datos'}}</h5>
                            </div>
                        </td>
                    </tr>
                    <slot v-else name="tbody"/>
                </tbody>
            </table>        
        </div>
        <Pagination :links="links" @PageChanged="HandlePageChanged" :loading="loading"/>
    </div>
</template>

<script>
import { defineComponent, ref, onMounted } from 'vue'
import store from '@/store/index'
import Pagination from '@/components/layout/Pagination.vue'
import servicios  from '@/services/otros'

export default defineComponent({
    props: [  
        'classTable',
        'columnas',
        'url',
    ],
    setup(props, { emit }) {
        const pageSize = ref(10)
        const loading = ref(true)
        const data = ref([])
        const url = ref(store.state.URL_SERVER + props.url)
        const links = ref([])
        const busqueda = ref(null);
        const filtro = ref(null);

        const fetch = async() => {
            loading.value = true;
            emit('handleChangedLoading',loading.value)
            const result = await servicios.datosTabla({filtro:filtro.value, busqueda:busqueda.value, results: pageSize.value,}, url.value);
            data.value = result.data;
            links.value = result.links;
            loading.value = result.cargando;
            emit('handleChangedData', data.value)
            emit('handleChangedLoading',loading.value)
        }

        const HandlePageChanged = async(_url) => {
            url.value = _url;
            await fetch();
        }

        const handleActualizarDatos = async() => {
            await fetch();
        }

        const handleChangeFiltro = async(_filtro) => {
            filtro.value = _filtro;
            await fetch();
        }

        onMounted(() => {
            fetch();
        })

        return {
            url,
            data,
            links,
            loading,
            busqueda,
            HandlePageChanged,
            handleChangeFiltro,
            handleActualizarDatos,
        }
    },
    components: {
        Pagination,
    },
})
</script>
<style  scoped>
    /* th:first-child{
        position:sticky;
        left:0px;
    }
    td:first-child
    {
        position:sticky;
        left:0px;
    } */
</style>