<template>
    <div class="modal fade show" ref="ModalRef" id="modalCenter" tabindex="-1" aria-hidden="true" data-bs-backdrop="static">
      <div class="modal-dialog" :class="size ?? 'modal-xl'">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close" @click="$emit('handleActualizarDatosTabla');"/>
          </div>
          <div class="container-fluid mb-2">
            <slot name="cardBody"></slot>
          </div>
        </div>
      </div>
    </div>
</template>
<script>
 
import { defineComponent} from 'vue'
import { Modal } from 'bootstrap';
export default defineComponent({
  props:['size'],
  methods: {
    showModal(){
      new Modal(this.$refs.ModalRef).show();
    },
  },
})
</script>
